@import url(https://fonts.googleapis.com/css?family=Source+Serif+Pro:600&display=swap);
html{
  scroll-behavior: smooth;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


a:hover {
  color: #00FF00;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

a {
  color: white;
}

a:hover {
  color: #1f822b;
  -webkit-transition: color .3s ease-in-out;
  transition: color .3s ease-in-out;
}

.footerCustom{
  background: #303030;
  height: 150px;
}

.borderBottom{
  color: inherit;
  border-bottom: 3px solid #179038;
  padding-bottom: 6px;
}

.upPosition{
  position: relative;
  left: 50%;
  bottom: 40px;
  font-size: 40px;
}

.customBanner{
  text-align: center;
}

.about{
  background: #2B2B2B;
  padding: 150px 0 100px 0;
  color: #fff;
}

.resume{
  padding: 80px 0 100px 0;
}
.dot {
  margin:2px;
  height: 5px;
  width: 5px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
}

.imageIcon{
  height: 200px;
  width: 200px;
}

.flex{
  display: flex;
  flex-flow: wrap;
  justify-content: space-evenly;
}

.flexContent{
  height: 450px;
  width: 240px;
  text-align: center;
}

.resumeContent{
  font: 16px/30px 'Source Serif Pro', serif;
  color: #6E7881;
  font-style: italic;
}
.aboutContent{
  line-height: 30px;
  color: #7A7A7A;
}

.profilepic{
  position: relative;
  width: 150px;
  height: 150px;
  border-radius: 25%;

}

.navbar{
  background: rgba(0,0,0, 0.6);
}

.navbarCustom{
  background-color: #17a2b8;
}

Link{
  color:white;
}
span{
  color:#fff
}
h3{
  color: gray;
}
.jumbotron{
  position: relative;
  top: 200px;
  padding: 20px;
  background-color: rgba(0,0,0, 0.6);
  border: 1px solid #6c757d;
  border-radius: 25px;
}

.social{
  color: 'white';
  list-style: none;
  font-size: 50px ;
  margin-left:30%
}

i{
  cursor: pointer;
}
.marginRight{
    margin-right: 20px;
}
.vl {
  border-left: 2px solid #343a40;
  height: auto;
}

.divUser{
  position: static;
  overflow-y: scroll;
  height: 550px;
}

.customDiv{
  margin: 10px;
  border: 1px solid;
  width:100%;
  cursor: pointer;
  box-shadow: 2px 4px #17a2b8;

}

#nprogress .bar {
  background-color: red;
  height: 5px;
}
 
#nprogress .spinner-icon {
  
  border-top-color: #f4590a;
  border-left-color: #f4590a;
  
}

label{
  color:#17a2b8;
  font-weight: 600;

}
.btn-secondary {
  color: #fff;
  background-color: #17a2b8;
  border-color: #17a2b8;}

#nprogress .peg {
  box-shadow: 0 0 10px #f4590a, 0 0 5px #f4590a;
}
.form-login {
  background-color: #fbf8f8;
  padding-top: 10px;
  padding-bottom: 20px;
  padding-left: 20px;
  padding-right: 20px;
  border-radius: 15px;
  border-color:#d2d2d2;
  border-width: 5px;
  box-shadow:0 1px 0 #cfcfcf;
  margin:30px;
 
  box-shadow: 5px 5px 10px 10px #88888859;

}

.toasts {
  bottom: 1000px;
  position: relative;
  right: 24px;
  width: 240px;
  top:200px
}
